header .navbar {
    z-index: 1000;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateY(0);
    transition: $transition;
    background: transparent;
    &.sticky {
        background: #0d131f;
        box-shadow: 11px 8px 37px 0 rgba(38, 40, 128, 0.1);
        padding: 0.8rem 0.4rem;
        @include media-breakpoint-up(md) {
            padding: 1rem 0.4rem;
        }
        .nav-link {
            color: $second;
        }
    }
}

.nk-dark .navbar {
    background: #0d131f;
}

.header-unpin .navbar {
    @include media-breakpoint-up(md) {
        transform: translateY(-110px);
    }
}

.header-pin .navbar {
    transform: translateY(0);
}

.navbar-overide {
    padding: {
        top: rem(15px);
        bottom: rem(10px);
    }
    @include media-breakpoint-down(md) {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }
    @include media-breakpoint-up(md) {
        padding: {
            top: rem(10px);
            bottom: rem(10px);
        }
    }
    @include media-breakpoint-up(xxlg) {
        padding: {
            top: rem(25px);
            bottom: rem(20px);
        }
    }
}

.navbar-nav {
    align-items: center;
}

.nav-link {
    font-family: $fnt-m !important;
    color: $white !important;
    display: inline-flex;
    font-weight: 700;
    // text-transform: uppercase;
    position: relative;
    @include media-breakpoint-up(md) {
        font-size: rem(14px);
        line-height: rem(24px);
    }
    @include media-breakpoint-up(xxlg) {
        font-size: rem(16px);
        line-height: rem(26px);
    }
    margin: {
        left: rem(12px) !important;
        right: rem(12px) !important;
    }
    @include media-breakpoint-up(xxl) {
        margin: {
            left: rem(16px) !important;
            right: rem(16px) !important;
        }
    }
    padding: {
        top: 0 !important;
        bottom: 0 !important;
        left: 0 !important;
        right: 0;
    }
    &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: -0.2rem;
        width: 100%;
        height: 1px;
        background-color: $white;
        transition: $transition;
        transform-origin: left;
        transform: scaleX(0);
    }
    &:hover::after,
    &.active::after {
        transform: scaleX(1);
    }
    &.active {
        color: $white !important;
    }
}

.zm-brand-logo {
    @include media-breakpoint-down(md) {
        height: 20px;
    }
}

.zw-nav-search {
    cursor: pointer;
}

.zw-mobile-hamberg {
    position: relative;
}

// Header widget sticky onscrolll
.em-widgetsticky-wrap {
    min-height: 100px;
    &__info {
        display: flex;
        align-items: center;
        width: 90%;
        // expaning
        &.expanded {
            .em-header-widgetpanel {
                display: none;
                transition: all 0.5s ease;
            }
            .em-header-widgetnav {
                display: flex;
                transition: all 0.5s ease;
            }
        }
    }
}

.nav-toggle {
    user-select: none;
    cursor: pointer;
    height: auto;
    left: 0;
    position: relative;
    width: 40px;
    z-index: 2;
    display: table;
    &-bar {
        position: absolute;
        top: 25%;
        transform: translateY(-25%);
        transition: all 0.5s ease;
        background: $white;
        height: 2px;
        width: 16px;
        left: 0;
        border-radius: 20px;
        &::before,
        &::after {
            content: "";
            position: absolute;
            top: 25%;
            transform: translateY(-25%);
            transition: all 0.5s ease;
            background: $white;
            height: 2px;
            width: 20px;
            left: 0;
            border-radius: 20px;
        }
        &::before {
            margin-top: -7px;
            // width: 17px;
        }
        &::after {
            margin-top: 6px;
            width: 12px;
        }
    }
}

// header transparent
.navbar-transparent {
    box-shadow: 0 9px 32px rgba(89, 92, 219, 0.05);
    background: $white !important;
    // padding: 0.4rem;
    .navbar {
        padding: 0.4rem;
    }
    &.sticky {
        background: white !important;
    }
}

.nav-item.ui.dropdown {
    @include media-breakpoint-down(lg) {
        width: auto !important;
    }
}

.navbar-collapse {
    @include media-breakpoint-down(lg) {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100vh;
        bottom: 0;
        background: #0D131F;
        z-index: 10;
        transform: translateX(-100%);
        transition: $transition;
        overflow-y: auto;
        &.open-hamberg {
            transform: translateX(0%);
        }
        .navbar-list {
            padding-top: 6rem !important;
        }
        .nav-item {
            margin-bottom: 1.2rem !important;
        }
        .nav-link {
            border-bottom: 1px solid rgba($color: #192954, $alpha: 0.1);
            font-size: 18px !important;
            line-height: 28px !important;
            padding: {
                left: 1rem !important;
                right: 1rem !important;
                bottom: 1.2rem !important;
            }
            @include media-breakpoint-down(lg) {
                text-align: left;
            }
            // text-transform: capitalize;
            display: block;
            &::after {
                display: none;
            }
        }
    }
}

.zw-close-btn {
    display: none;
}

.zw-mobile-hamberg {
    display: block;
    &.active .nav-toggle {
        display: none;
    }
}

.body-fixed {
    overflow: hidden;
    .navbar-brand,
    .zw-mobile-hamberg {
        z-index: 100;
    }
    .zw-close-btn {
        display: block;
    }
}

.navbar-brand {
    svg {
        @include media-breakpoint-down(md) {
            width: 94px;
            height: 13px;
        }
    }
}

// Hamberger
nav {
    display: flex;
    align-items: center;
    padding: 0.5rem 2rem 1rem 2rem;
}

// Responsive CSS
@media (max-width: 991px) {
    // transparent menu
    .nav-mobile-transparent {
        box-shadow: 0 9px 32px rgba(89, 92, 219, 0.05);
        background-color: #ffffff !important;
        .hamburger-menu {
            background: url(../img/icons/menuIconTransparent.svg) no-repeat;
        }
        .header-unpin .nav-mobile {
            background-color: white;
        }
    }
}

.navbar .nav-item {
    @include media-breakpoint-down(lg) {
        width: 100% !important;
    }
}

.nk-mainheader {
    .navbar-collapse {
        justify-content: center;
    }
}

.nk-mainheader {
    &.solid {
        background: #0D131F;
    }
}