.Nh-banner {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    @include media-breakpoint-down(md) {
        background: #0D131F;
    }
    .fp-tableCell {
        @include media-breakpoint-down(md) {
            vertical-align: top;
        }
    }
    &__content {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 8;
    }
    &__video {
        width: 100%;
        height: 100%;
        position: absolute;
    }
    &__overlay {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        z-index: 2;
        background: radial-gradient(50% 50% at 50% 50%, rgba(25, 123, 113, 0.3) 0%, rgba(0, 0, 0, 0.6) 100%);
    }
    &__img {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: 100%;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        @include media-breakpoint-down(md) {
            position: relative;
            height: 360px;
        }
    }
    .container {
        position: relative;
        z-index: 3;
        &.mp-home {
            @include media-breakpoint-down(md) {
                padding-top: 32px;
                padding-bottom: 32px;
            }
        }
    }
    .Nk-scroll {
        display: none;
        @include media-breakpoint-up(lg) {
            display: flex;
            margin: 24px 0 32px;
        }
        @include media-breakpoint-up(xxl) {
            margin: 50px 0 32px;
        }
        span {
            font-weight: 600;
            font-size: 12px;
            line-height: 28px;
            text-transform: uppercase;
            color: #FFFFFF;
            padding-left: 8px;
        }
    }
    .Nk-notification {
        width: 100%;
        position: relative;
        &__wrp {
            display: flex;
            align-items: center;
        }
        padding: 12px 0;
        background-color: $black;
        &__text {
            margin-left: 20px;
            font-weight: 600;
            font-size: 20px;
            line-height: 32px;
            color: #FFFFFF;
            @include media-breakpoint-down(xxl) {
                font-size: 16px;
                line-height: 24px;
            }
            @include media-breakpoint-down(md) {
                font-size: 12px;
                line-height: 16px;
            }
        }
    }
}

// base
.flip {
    position: relative;
    >.front,
    >.back {
        display: block;
        transition-timing-function: cubic-bezier(.175, .885, .32, 1.275);
        transition-duration: .8s;
        transition-property: transform, opacity;
    }
    >.front {
        transform: rotateY(0deg);
    }
    >.back {
        position: absolute;
        opacity: 0;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        transform: rotateY(-180deg);
    }
    &:hover {
        >.front {
            transform: rotateY(180deg);
        }
        >.back {
            opacity: 1;
            transform: rotateY(0deg);
        }
    }
    &.flip-vertical {
        >.back {
            transform: rotateX(-180deg);
        }
        &:hover {
            >.front {
                transform: rotateX(180deg);
            }
            >.back {
                transform: rotateX(0deg);
            }
        }
    }
}

// custom
.flip {
    position: relative;
    display: inline-block;
    >.front,
    >.back {
        display: block;
        // color: white;
        width: inherit;
        background-size: cover!important;
        background-position: center!important;
        // padding: 1em 2em;
        background: $white;
    }
}

.Nk-engage {
    background: #0D131F;
    &__card {
        width: 100%;
        margin-bottom: 20px;
        @include media-breakpoint-up(xxl) {
            margin-bottom: 40px;
        }
        &__img {
            width: 100%;
            height: 132px;
            @include media-breakpoint-up(lg) {
                // height: 180px;
                height: 250px;
            }
            @include media-breakpoint-up(xxl) {
                height: 311px;
            }
            @include media-breakpoint-up(xxlg) {
                height: 511px;
            }
            @include media-breakpoint-up(xxlg) {
                height: 511px;
            }
            @media (min-width:1920px) and(max-height:1281px) {
                height: 328px;
            }
            position: relative;
        }
        .front {
            position: relative;
            &::after {
                position: absolute;
                content: '';
                background: #0D131F;
                opacity: 0.7;
                width: 100%;
                height: 100%;
                left: 0;
                right: 0;
                bottom: 0;
                top: 0;
                z-index: 5;
            }
            .info {
                padding: 40px;
                position: absolute;
                bottom: 0;
                left: 0;
                z-index: 6;
                @include media-breakpoint-up(xxl) {
                    padding: 80px;
                }
            }
        }
        .back {
            padding: 18px 20px;
            @include media-breakpoint-up(xl) {
                // padding: 35px 32px;
                padding: 20px 32px;
            }
            @include media-breakpoint-up(xxlg) {
                padding: 65px 80px;
            }
            @media (min-width:1440px) and(max-height:1281px) {
                padding: 40px;
            }
        }
        span {
            font-weight: 600;
            font-size: 10px;
            line-height: 28px;
            text-transform: uppercase;
            @include media-breakpoint-up(xl) {
                font-size: 12px;
            }
        }
        h5 {
            font-weight: 600;
            font-size: 20px;
            line-height: 27px;
            letter-spacing: -0.04em;
            color: #FFFFFF;
            @include media-breakpoint-up(xl) {
                font-size: 24px;
                line-height: 32px;
            }
            @include media-breakpoint-up(xxl) {
                font-size: 40px;
                line-height: 50px;
            }
        }
        .fnt-20 {
            @include media-breakpoint-between(lg, xxl) {
                font-size: 16px;
                line-height: 24px
            }
        }
    }
}

//plan
.Nk-plan {
    background: #F3F3F3;
    .post {
        // margin-top: 42px;
        padding: 50px !important;
        background: #0D131F;
        margin-bottom: 0;
        @include media-breakpoint-down(xxl) {
            // width: 100%;
            padding: 24px !important;
        }
        @include media-breakpoint-down(md) {
            width: 100%;
            padding: 24px !important;
        }
        &__item {
            &:not(:last-child) {
                padding-bottom: 20px !important;
                @include media-breakpoint-down(md) {
                    padding-bottom: 16px !important;
                    border-bottom: 1px solid rgba($color: #fff, $alpha: .1);
                }
            }
            &:not(:first-child) {
                padding-top: 20px !important;
                @include media-breakpoint-down(md) {
                    padding-top: 16px !important;
                }
            }
            .date {
                font-weight: 600;
                font-size: 12px;
                line-height: 28px;
                text-transform: uppercase;
                color: #FFFFFF;
            }
            .location {
                font-weight: 600;
                font-size: 20px;
                line-height: 32px;
                color: #FFFFFF;
                @include media-breakpoint-down(md) {
                    font-size: 12px;
                }
            }
        }
    }
    &__card {
        @include media-breakpoint-down(md) {
            margin-top: 24px;
        }
        &__img {
            width: 100%;
            height: 299px;
            object-fit: cover;
            @include media-breakpoint-down(xxl) {
                object-position: top;
                height: 220px;
            }
            @include media-breakpoint-down(md) {
                object-position: top;
                height: 180px;
            }
        }
        &__body {
            margin-top: 24px;
            @include media-breakpoint-up(xxlg) {
                margin-top: 40px;
            }
            .Nk-h3 {
                @include media-breakpoint-down(xxl) {
                    flex: 1;
                }
            }
            &__text {
                flex: 1;
                font-weight: 600;
                font-size: 20px;
                line-height: 32px;
                color: #000000;
                display: none;
                @include media-breakpoint-up(xxlg) {
                    display: block;
                }
            }
            // .Nk-h3 {
            //     opacity: 0;
            // }
            .btn-wrp {
                margin-top: 20px;
                // @include media-breakpoint-between(xl, xxlg) {
                // }
            }
        }
    }
}

