.delivary {
    &__img {
        object-fit: cover;
        width: 100%;
        height: 149px;
        @include media-breakpoint-up(md) {
            height: 346px;
        }
        @include media-breakpoint-up(xxlg) {
            height: 480px;
        }
        // @include media-breakpoint-up(xxlg) {
        //     height: 670px;
        // }
    }
    .logoWrp {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 200px;
        height: 95px;
        margin: auto;
        border: 1px solid rgba($color: $white, $alpha: .1);
        @include media-breakpoint-down(md) {
            height: 52px;
        }
    }
    .js-logo-progressSLider {
        margin-top: 50px;
        @include media-breakpoint-down(md) {
            margin-top: 32px;
        }
        @include media-breakpoint-down(xxl) {
            margin-top: 20px;
        }
        .swiper-slide {
            .logoWrp {
                &::before {
                    position: absolute;
                    content: '';
                    background-color: #FFFFFF;
                    width: 0;
                    height: 4px;
                    left: 0;
                    bottom: 0;
                    transition: ease width 5s;
                }
                img {
                    @include media-breakpoint-down(md) {
                        // width: 100%;
                        object-fit: cover;
                    }
                }
                @include media-breakpoint-down(md) {
                    width: 100%;
                }
                .semens {
                    @include media-breakpoint-down(md) {
                        width: 64px;
                        height: 17px;
                    }
                }
                .m3 {
                    @include media-breakpoint-down(md) {
                        width: 35px;
                        height: 42px;
                    }
                }
                .ray {
                    @include media-breakpoint-down(md) {
                        width: 61.63px;
                        height: 18.79px;
                    }
                }
                .backer {
                    @include media-breakpoint-down(md) {
                        width: 61.63px;
                        height: 18.79px;
                    }
                }
                .potent {
                    @include media-breakpoint-down(md) {
                        width: 61.63px;
                        height: 18.79px;
                    }
                }
            }
        }
        .swiper-slide-active {
            .logoWrp {
                position: relative;
                &::after {
                    position: absolute;
                    content: '';
                    background: #FFFFFF;
                    opacity: 0.1;
                    height: 4px;
                    width: 100%;
                    bottom: 0;
                    left: 0;
                    z-index: 3;
                    right: 0;
                }
                &::before {
                    width: 100% !important;
                    transition: ease width 5s;
                }
            }
        }
    }
    .slider-logo {
        position: relative;
        // overflow: hidden;
        .js-arrow-left {
            @include media-breakpoint-down(md) {
                display: none;
            }
            position: absolute;
            left: -90px;
            transform: translateY(-50%);
            top: 50%;
            @include media-breakpoint-down(lg) {
                left: -34px;
            }
        }
        .js-arrow-right {
            position: absolute;
            right: -90px;
            transform: translateY(-50%) rotate(180deg);
            top: 50%;
            @include media-breakpoint-down(lg) {
                right: -34px;
            }
            @include media-breakpoint-down(md) {
                display: none;
            }
        }
    }
}

.text-arrows {
    margin-top: 4px;
    .js-arrow-right {
        display: flex;
        align-items: center;
    }
    .js-arrow-left {
        display: flex;
        align-items: center;
        transform: rotate(180deg);
    }
}

.delivaryImg__wrp {
    overflow: hidden;
    position: relative;
    .overlay {
        position: absolute;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($color: $white, $alpha: .8);
        z-index: 3;
    }
}

