.kn-banner {
    padding-top: 160px;
    padding-bottom: 100px;
    background: #0D131F;
    @include media-breakpoint-down(md) {
        padding-top: 70px;
        padding-bottom: 24px;
    }
    &__img {
        width: 100%;
        height: 290px;
        object-fit: cover;
        @include media-breakpoint-up(md) {
            height: 380px;
        }
        @include media-breakpoint-up(lg) {
            height: 480px;
        }
        @include media-breakpoint-up(xxl) {
            height: 680px;
        }
    }
    &__wrp {
        @include media-breakpoint-down(md) {
            margin-top: 24px;
        }
        .Nk-h2 {
            @include media-breakpoint-down(md) {
                font-size: 40px;
                line-height: 50px;
            }
        }
        .mt-42 {
            @include media-breakpoint-down(md) {
                margin-top: 24px;
            }
        }
        @include media-breakpoint-up(xxlg) {
            max-width: 450px;
            margin-left: 100px;
        }
    }
}

.contact-banner {
    .kn-banner__wrp {
        @include media-breakpoint-up(xxlg) {
            max-width: 100%;
            margin-left: 100px;
        }
    }
}

.kn-cards {
    &__img {
        width: 100%;
        // width: 438px;
        height: 438px;
        object-fit: cover;
        @include media-breakpoint-down(xxl) {
            height: 357px;
        }
        @include media-breakpoint-down(xl) {
            height: 340px;
        }
        @include media-breakpoint-down(md) {
            height: 180px;
        }
    }
}

.nk-tabs {
    padding-bottom: 0;
}

.js-niceSelect {
    border: none;
    height: auto;
    min-height: auto;
    @include media-breakpoint-down(md) {
        padding-left: 12px;
        // padding-right: 12px;
    }
    .current {
        font-size: 20px;
        font-weight: 700;
    }
}

.filter-sort {
    display: flex;
    align-items: center;
}

.nice-select .option {
    padding: 8px !important;
    min-height: 15px;
    height: auto;
}

.nice-select:after {
    border-bottom: 1.5px solid #000;
    border-right: 1.5px solid #000;
    width: 8px;
    height: 8px;
    @include media-breakpoint-down(md) {
        width: 8px;
        height: 8px;
    }
}

.kn-cards-wrp {
    .Nk-card {
        margin-bottom: 100px;
        @include media-breakpoint-down(xl) {
            margin-bottom: 45px;
        }
        @include media-breakpoint-down(md) {
            margin-bottom: 32px;
        }
    }
}

.filter-wrp {
    align-items: center;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    @include media-breakpoint-down(xl) {
        flex-direction: column-reverse;
        align-items: end;
    }
    .Nk-tabs {
        margin-top: 24px;
        margin-bottom: 32px;
    }
}

.logo-wrp {
    a {
        text-decoration: none;
        margin-left: 10px;
    }
    img {
        @include media-breakpoint-down(xxl) {
            width: 24px
        }
    }
}

.knwld-banner {
    position: relative;
    .js-arrow-right,
    .js-arrow-left {
        position: absolute;
        top: 50%;
        @include media-breakpoint-down(md) {
            display: none;
        }
    }
}

.kn-cards {
    text-decoration: none;
    &:hover {
        h3 {
            color: #051D60;
        }
    }
    &__imgWrp {
        overflow: hidden;
    }
    &__img {
        transform: scale(1);
        transition: .5s all ease;
        &:hover {
            transform: scale(1.2);
            transition: .5s all ease;
        }
    }
}

.js-arrow-right {
    left: -40px;
}

.js-arrow-left {
    right: -40px;
    transform: rotate(180deg);
}