// Play buttons
.em-play-btn {
    height: rem(40px);
    width: rem(40px);
    @include border-radius(50%);
    border: 2px solid $white;
    @include media-breakpoint-up(lg) {
        border: 4px solid $white;
        height: rem(70px);
        width: rem(70px);
    }
    &--abs {
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        left: 50%;
        z-index: 10;
        cursor: pointer;
        &:hover {
            background: #60c6f7;
            transition: background 0.4s ease-in;
            border: 3px solid $white;
        }
    }
}

.play-icon {
    height: rem(15px);
    position: relative;
    left: 2px;
    @include media-breakpoint-up(lg) {
        height: rem(28px);
    }
}

// Slider icons
.icon-arrow {
    font-size: 20px;
    &--white {
        color: $white;
    }
}

// BUTTON FOCUS
.btn-check:focus+.btn,
.btn:focus {
    outline: 0;
    box-shadow: none;
    border: 1px solid #e2e2ff;
}


/* Saqui */

.btn-saq {
    position: relative;
    overflow: hidden;
    // color: #fff;
    // background: #37474f;
    -webkit-transition: background-color 0.3s ease-in, color 0.3s ease-in;
    transition: background-color 0.3s ease-in, color 0.3s ease-in;
}

// .btn-saq.button--inverted {
//     background: #fff;
//     color: #37474f;
// }
.btn-saq::after {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    // padding: 1em 2em;
    text-align: center;
    padding: 11px 45px;
    // padding: 17px 25px;
    // color: #37474f;
    -webkit-transform-origin: -25% 50%;
    transform-origin: -25% 50%;
    -webkit-transform: rotate3d(0, 0, 1, 45deg);
    transform: rotate3d(0, 0, 1, 45deg);
    -webkit-transition: -webkit-transform 0.3s ease-in;
    transition: transform 0.3s ease-in;
    @include media-breakpoint-up(md) {
        padding: 14px 25px;
    }
    @include media-breakpoint-up(xxlg) {
        padding: 17px 25px;
    }
}

.btn--sm::after {
    padding: 8px 32px !important;
}

.btn--b-trans:hover {
    background-color: #051D60;
}

.btn--b-trans::after {
    color: #fff;
}

.btn--w::after {
    color: #fff;
}

.btn--trans::after {
    color: #051D60;
}

.btn--blue::after {
    color: #051D60;
}

.btn-saq.btn--blue:hover {
    color: $white;
}

.btn--blue-trans::after {
    color: $white;
}

.btn-saq:hover::after,
.btn-saq:hover {
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}

.btn-saq.btn--w:hover {
    background-color: #051D60;
    color: #051D60;
    border: 1px solid #051D60;
}

.btn--blue.btn--sm {
    i {
        font-size: 16px;
        margin-left: 6px;
    }
    &:hover {
        color: $white;
        // background-color: #;
        i {
            color: #051D60 !important;
            transform: translateX(8px);
            transition: all ease-in-out 0.5s;
        }
    }
}

.btn--trans:hover {
    background-color: $white;
    color: $white;
}

.btn--blue:hover {
    background-color: $white;
    color: $white;
    border: 1px solid #051D60;
}

.btn-saq:hover::after {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
    transform: rotate3d(0, 0, 1, 0deg);
}