    .contact-form {
        &__wrp {
            background: #FFFFFF;
            border: 1px solid #F1F1F1;
            box-sizing: border-box;
            box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.06);
            padding: 100px 0;
            @include media-breakpoint-down(lg) {
                padding: 32px 16px;
            }
        }
        .l-gry-from {
            @include media-breakpoint-down(md) {
                margin-top: 24px;
            }
            .mb-40 {
                @include media-breakpoint-down(md) {
                    margin-bottom: 4px;
                }
            }
        }
        .radio-group-container {
            display: flex;
            justify-content: flex-end;
            flex-wrap: wrap;
            @include media-breakpoint-down(md) {
                flex-direction: column;
            }
            @include media-breakpoint-between(xl, xxl) {
                justify-content: flex-start;
            }
            .radio-group {
                margin-right: 48px;
                @include media-breakpoint-down(lg) {
                    margin-bottom: 20px;
                }
            }
        }
        [type="radio"]:checked,
        [type="radio"]:not(:checked) {
            position: absolute;
            left: -9999px;
        }
        [type="radio"]:checked+label,
        [type="radio"]:not(:checked)+label {
            position: relative;
            padding-left: 28px;
            cursor: pointer;
            font-weight: 600;
            font-size: 14px;
            line-height: 24px;
            color: #000000;
            opacity: 0.8;
        }
        [type="radio"]:checked+label:before,
        [type="radio"]:not(:checked)+label:before {
            content: '';
            position: absolute;
            left: 0;
            top: 3px;
            width: 18px;
            height: 18px;
            border: 1px solid rgba($color: #0D131F, $alpha: 0.4);
            border-radius: 100%;
            background: #fff;
        }
        [type="radio"]:checked+label:before {
            border: 1px solid #051D60;
        }
        [type="radio"]:checked+label:after,
        [type="radio"]:not(:checked)+label:after {
            content: '';
            width: 12px;
            height: 12px;
            background: #051D60;
            position: absolute;
            top: 6px;
            left: 3px;
            border-radius: 100%;
            -webkit-transition: all 0.2s ease;
            transition: all 0.2s ease;
        }
        [type="radio"]:not(:checked)+label:after {
            opacity: 0;
            -webkit-transform: scale(0);
            transform: scale(0);
        }
        [type="radio"]:checked+label:after {
            opacity: 1;
            -webkit-transform: scale(1);
            transform: scale(1);
        }
    }
    
    .our-location {
        .mt-42 {
            @include media-breakpoint-down(md) {
                margin-top: 16px;
            }
        }
        .Nk-locatins {
            .col-md-6 {
                display: flex;
            }
            .col-md-6:not(:first-child) {
                @include media-breakpoint-down(md) {
                    margin-top: 32px;
                }
            }
        }
        &__card {
            display: flex;
            flex-direction: column;
            &__img {
                height: 361px;
                max-width: 100%;
                width: 100%;
                object-fit: cover;
                @include media-breakpoint-down(md) {
                    // height: 166px;
                    height: 215px;
                }
            }
            &__body {
                display: flex;
                flex-direction: column;
                flex: 1;
                max-width: 332px;
                margin-top: 60px;
                @include media-breakpoint-down(md) {
                    margin-top: 16px;
                    max-width: 100%;
                }
                p {
                    flex: 1;
                }
                .Nk-h3 {
                    @include media-breakpoint-down(md) {
                        font-size: 20px;
                        line-height: 32px;
                        /* identical to box height, or 160% */
                        letter-spacing: -0.04em;
                        color: #000000;
                    }
                }
                .fnt-20 {
                    @include media-breakpoint-down(md) {
                        font-size: 14px;
                    }
                }
            }
        }
    }
    
    .linkedIn {
        &__card {
            background: #FFFFFF;
            border: 1px solid #B5B5B5;
            box-sizing: border-box;
            // padding: 24px;
            &__head {
                padding: 24px 24px 0 24px;
                @include media-breakpoint-down(md) {
                    padding: 16px 16px 0 16px;
                }
                display: flex;
                justify-content: space-between;
                .fnt-12 {
                    @include media-breakpoint-down(md) {
                        font-size: 10px !important;
                    }
                }
                .info {
                    display: flex;
                    &__details {
                        margin-left: 14px;
                        .fnt-16 {
                            @include media-breakpoint-down(md) {
                                font-size: 14px !important;
                                line-height: 24px;
                            }
                        }
                    }
                    &__logo {
                        width: 40px;
                        height: 40px;
                    }
                }
                .date {
                    align-self: flex-end;
                }
            }
            .scroll-container {
                margin-top: 40px;
                margin-bottom: 24px;
                @include media-breakpoint-down(md) {
                    margin-top: 24px;
                }
            }
            &__body {
                padding: 0 24px;
                height: 363px;
                margin-bottom: 24px;
                @include media-breakpoint-down(md) {
                    padding: 0 16px;
                }
                .fnt-14 {
                    @include media-breakpoint-down(md) {
                        font-size: 14px;
                    }
                }
                &__img {
                    width: 100%;
                    object-fit: cover;
                    height: 263px;
                    margin: 20px 0;
                    @include media-breakpoint-down(md) {
                        margin: 16px 0;
                        height: 182px;
                    }
                }
            }
            &__footer {
                &__wrp {
                    align-items: center;
                    display: flex;
                    border-top: 1px solid #DDDDDD;
                    padding-top: 24px;
                    a {
                        text-decoration: none;
                        img {
                            transition: transform .5s ease;
                        }
                        &:hover {
                            img {
                                transform: scale(1.2);
                                transition: transform .5s ease;
                            }
                        }
                    }
                }
                padding:0 24px 24px;
                @include media-breakpoint-down(md) {
                    padding: 0 16px 24px;
                }
            }
        }
    }
    
