body {
    font-family: $fnt-m;
    font-weight: $fnt-400;
    overflow-x: hidden;
    color: $primary;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    // background-color: $bg-pri;
    p {
        font-size: rem(14px);
        line-height: rem(22px);
        margin: 0;
        padding: 0;
        @include media-breakpoint-up(lg) {
            font-size: rem(16px);
            line-height: rem(26px);
        }
        @include media-breakpoint-up(xxlg) {
            font-size: rem(21px);
            line-height: rem(32px);
        }
    }
}

.container {
    @include media-breakpoint-up(xxlg) {
        max-width: 1520px;
    }
}

.Nk-px-mob {
    @include media-breakpoint-down(md) {
        padding: 0 !important;
    }
}

.Nk-pe-mob {
    @include media-breakpoint-down(md) {
        padding-right: 0;
    }
}

.Nk-only-desktop {
    @include media-breakpoint-down(md) {
        display: none !important;
    }
}

.Nk-only-mobile {
    @include media-breakpoint-up(lg) {
        display: none !important;
    }
}

.Nk-only-mobile-md {
    @include media-breakpoint-up(md) {
        display: none !important;
    }
}

.Nk-only-mobile-hidden {
    @include media-breakpoint-down(md) {
        overflow: hidden;
    }
}

.Nk-only-tablet-hidden {
    @include media-breakpoint-down(lg) {
        overflow: hidden;
    }
}

.Nk-only-desktop-hidden {
    @media (max-width: 1280px) {
        overflow: hidden;
    }
}

// paddings
.Nk-topspace8 {
    padding-top: 6rem;
    @include media-breakpoint-up(md) {
        padding-top: 8rem;
    }
}

// images scaleout
.Nk-scaleout-09 {
    @include media-breakpoint-up(lg) {
        transform: scale(0.9);
