//button style
.btn {
    font-size: 14px;
    line-height: 26px;
    padding: 11px 45px;
    width: 100%;
    border-radius: 50px !important;
    font-weight: 700;
    text-align: center;
    @include media-breakpoint-up(md) {
        width: auto;
        padding: 14px 25px;
        font-size: 16px;
        line-height: 26px;
    }
    @include media-breakpoint-up(xxlg) {
        width: auto;
        padding: 17px 45px;
        font-size: 16px;
        line-height: 26px;
    }
    &--sm {
        padding: 8px 32px;
        // display: flex;
        // align-items: center;
        i {
            width: 24px !important;
            margin-left: 16px;
            transition: all ease-in-out 0.5s;
            font-size: 24px;
        }
        &:hover {
            color: $white;
            background-color: #051D60;
            i {
                color: $white;
                transform: translateX(8px);
                transition: all ease-in-out 0.5s;
            }
        }
    }
    &--w {
        background-color: $white;
        color: #051D60;
        &:hover {}
    }
    &--trans {
        color: $white;
        border: 1px solid #FFFFFF;
        background-color: transparent;
    }
    &--b-trans {
        @extend .clr-0d;
        border: 1px solid #0D131F;
    }
    &--blue-trans {
        @extend .clr-0d;
        border: 1px solid #051D60;
    }
    &--eq-width {
        @include media-breakpoint-up(md) {
            min-width: 205px;
        }
    }
    &--blue {
        // padding: 17px 72px;
        background: #051D60;
        color: $white;
        &.btn-disable {
            background-color: #bec3d3e6;
            pointer-events: none;
        }
        &:hover {
            color: #051D60 !important;
        }
    }
}

.backtoHome {
    &:hover {
        color: #FFFFFF !important;
        i {
            transition: all ease .5s;
            transform: translateX(6px);
        }
    }
    i {
        margin-left: 6px;
        transition: all ease .5s;
    }
}

