.Nk-card {
    &--lg {
        &__img {
            width: 100%;
            height: 400px;
            @include media-breakpoint-up(xxlg) {
                height: 680px;
            }
            @media (min-width:1440px) and(max-height:1281px) {
                height: 405px;
            }
        }
        &__body {
            margin-top: 24px;
            &__date {
                color: #051D60;
            }
            &__tag {
                color: #8D8D8D;
            }
            span {
                font-weight: 600;
                font-size: 12px;
                line-height: 28px;
                text-transform: uppercase;
            }
        }
    }
    &--sm {
        @include media-breakpoint-between(xl, xxl) {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }
        &__card {
            display: flex;
            padding: 10px 0;
            &:first-of-type {
                @include media-breakpoint-up(xxl) {
                    padding-top: 0;
                }
            }
            &:nth-child(4) {
                display: none;
            }
            @include media-breakpoint-between(lg, xxlg) {
                &:nth-child(4) {
                    display: flex;
                }
            }
            @include media-breakpoint-down(md) {
                border-bottom: 1px solid rgba($color: #000000, $alpha: .1);
            }
            @include media-breakpoint-between(xl, xxl) {
                width: 46%;
                padding: 8px 0;
            }
            &__img {
                display: none;
                object-fit: cover;
                @include media-breakpoint-up(md) {
                    width: 160px;
                    height: 160px;
                    display: block;
                }
                @include media-breakpoint-up(xxl) {
                    width: 180px;
                    height: 180px;
                }
            }
            &__body {
                @include media-breakpoint-up(md) {
                    margin-left: 20px;
                }
                @include media-breakpoint-up(xxl) {
                    margin-left: 40px;
                }
                &__date {
                    color: #051D60;
                }
                &__tag {
                    color: #8D8D8D;
                }
                &__desc {
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 25px;
                    /* or 129% */
                    letter-spacing: -0.04em;
                    color: #000000;
                    @include media-breakpoint-up(xxl) {
                        font-size: 24px;
                        line-height: 31px;
                    }
                }
                span {
                    font-weight: 600;
                    font-size: 12px;
                    line-height: 28px;
                    /* identical to box height, or 233% */
                    text-transform: uppercase;
                }
            }
        }
    }
}

