.op-banner {
    background: #0D131F;
    display: flex;
    align-items: flex-end;
    padding: 120px 0 45px;
    @include media-breakpoint-up(md) {
        height: 90vh;
        padding: 80px 0;
    }
    &__search {
        margin-top: 48px;
        position: relative;
        @include media-breakpoint-up(lg) {
            margin-top: 80px;
        }
        input {
            width: 100%;
            padding: 27px 67px 27px 32px;
            background-color: transparent;
            border: 1px solid #FFFFFF;
            outline: none;
            box-shadow: none;
            color: $white;
            @include media-breakpoint-down(md) {
                padding: 12px 40px 12px 16px;
            }
            &::placeholder {
                font-weight: 600;
                font-size: 18px;
                line-height: 26px;
                letter-spacing: -0.04em;
                color: #FFFFFF;
                opacity: 0.6;
                @include media-breakpoint-down(md) {
                    font-size: 14px;
                    line-height: 24px;
                }
            }
        }
        &__btn {
            background-color: transparent;
            border: none;
            position: absolute;
            right: 24px;
            top: 50%;
            transform: translateY(-50%);
            @include media-breakpoint-down(md) {
                right: 8px;
            }
            img {
                @include media-breakpoint-down(md) {
                    width: 24px;
                    height: 24px;
                }
            }
        }
    }
}

