.custom-label.left {
    right: 13px;
    transform: translateX(100%);
    &::after {
        left: 3px;
    }
}

.custom-label.right {
    right: 0;
    transform: translateX(10px);
    &::after {
        left: inherit;
        right: 3px;
    }
}

.o-8 {
    opacity: .8;
}

.form-group {
    display: block;
    margin-top: 15px;
}

.form-group input {
    padding: 0;
    height: initial;
    width: initial;
    margin-bottom: 0;
    display: none;
    cursor: pointer;
}

.form-group label {
    position: relative;
    cursor: pointer;
    font-size: 16px;
    line-height: 26px;
    /* identical to box height, or 162% */
    /* Gray 3 */
    color: #8492A6;
}

.form-group label:before {
    content: '';
    -webkit-appearance: none;
    background-color: transparent;
    border: 1px solid #0D131F;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
    padding: 10px;
    display: inline-block;
    position: relative;
    vertical-align: middle;
    cursor: pointer;
    margin-right: 10px;
}

.form-group input:checked+label:after {
    content: '';
    display: block;
    position: absolute;
    top: 7px;
    left: 9px;
    width: 6px;
    height: 14px;
    border: solid #0D131F;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
}

.form-group input:checked+label {
    color: #000;
    font-weight: 700;
}

.btn-drp-wrp {
    display: flex;
    justify-content: center;
}

.or-ele {
    margin-top: 14px;
    position: relative;
    text-transform: uppercase;
    &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 40%;
        border: 1px solid #E8E8E8;
    }
    &::before {
        content: "";
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 40%;
        border: 1px solid #E8E8E8;
    }
}

.jobs-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 46px;
    @include media-breakpoint-down(md) {
        display: none;
    }
}

.page-link {
    border: none;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height, or 150% */
    color: #8492A6;
    font-weight: 700;
    &:focus {
        background-color: none;
        box-shadow: none;
    }
}

.page-item.active-item {
    .page-link {
        color: #000000;
    }
}

.italic {
    font-style: italic;
}

.skills {
    background: #F3F3F3;
}

.l-gry-from {
