        }
    }
    .input-msg {
        width: 100%;
        border: 1px solid rgba($color: #0D131F, $alpha: .4);
        box-sizing: border-box;
        border-radius: 28px;
        padding: 18px;
        height: 140px;
        @include media-breakpoint-down(md) {
            height: 100px;
        }
        &::placeholder {
            font-weight: 600;
            font-size: 14px;
            line-height: 24px;
            /* or 171% */
            color: #0D131F;
            opacity: 0.6;
        }
    }
}

.job-form {
    display: none;
    .grp-input {
        position: relative;
        .phonedrop {
            padding-left: 0;
            position: absolute;
            left: 17px;
            top: 50%;
            transform: translateY(-50%);
            border-radius: 0;
            border-right: 1px solid rgba($color: #0D131F, $alpha: .4);
        }
        .input-number {
            padding-left: 84px;
        }
        .current {
            font-weight: 600;
            font-size: 14px;
            line-height: 24px;
            color: #000000;
        }
    }
}

.dropzone {
    margin-top: 60px;
    padding: 56px;
    text-align: center;
    border: 1px dashed rgba($color: #0D131F, $alpha: .4);
    box-sizing: border-box;
    border-radius: 50px;
    @include media-breakpoint-down(lg) {
        margin-top: 42px;
    }
    @include media-breakpoint-down(md) {
        padding: 24px 32px;
        margin-top: 0;
    }
    .dropzone-container {
        cursor: pointer;
    }
    .file-input {
        display: none;
    }
    .drop-btn {
        border: 1px solid #051D60;
        box-sizing: border-box;
        border-radius: 49px;
        padding: 8px 16px;
        font-size: 14px;
        line-height: 24px;
        /* identical to box height, or 171% */
        text-align: center;
        color: #051D60;
        font-weight: 700;
        display: flex;
        justify-content: center;
        p {
            font-size: 14px;
            line-height: 24px;
            /* identical to box height, or 171% */
            text-align: center;
        }
    }
}

.job-modal {
    margin: 0 0 0 auto;
    height: 100vh;
    @include media-breakpoint-up(lg) {
        max-width: 767px;
    }
    @include media-breakpoint-up(xl) {
        max-width: 1160px;
    }
    .modal-content {
        border-radius: 0;
    }
    .modal-header {
        align-items: flex-start;
        padding: 20px 20px 16px;
        border: none;
        @include media-breakpoint-up(lg) {
            padding: 40px 60px 16px;
        }
        @include media-breakpoint-up(xxl) {
            padding: 80px 120px 30px;
        }
    }
    .modal-body {
        padding: 0 16px;
        @include media-breakpoint-up(lg) {
            padding: 0 60px 50px;
        }
        @include media-breakpoint-up(xxl) {
            padding: 0 120px 102px;
        }
    }
    .job-modal-footer {
        padding: 20px 20px 32px;
        background: #FFFFFF;
        border: 1px solid #F1F1F1;
        box-sizing: border-box;
        box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.06);
        @include media-breakpoint-up(lg) {
            padding: 20px 60px 32px;
        }
        @include media-breakpoint-up(xxl) {
            padding: 40px 120px 64px;
        }
        &__btn-group {
            @include media-breakpoint-down(md) {
                margin-top: 16px;
            }
            .btn {
                @include media-breakpoint-down(md) {
                    padding: 11px 24px;
                }
            }
        }
    }
    h6 {
        font-weight: 600;
        font-size: 20px;
        line-height: 32px;
        /* identical to box height, or 160% */
        letter-spacing: -0.04em;
        color: #000000;
        @include media-breakpoint-down(md) {
            font-size: 16px;
            line-height: 24px;
        }
        &+.fnt-20 {
            @include media-breakpoint-down(md) {
                font-size: 14px !important;
                line-height: 24px;
            }
        }
    }
    .job-list {
        li {
            position: relative;
            padding-left: 20px;
            &::before {
                position: absolute;
                content: '';
                left: 0;
                top: 12px;
                width: 8px;
                height: 8px;
                background-color: black;
                border-radius: 50%;
            }
        }
    }
    .js-scroll-container {
        height: 100%;
        &__wrp {
            margin-right: 12px;
            @include media-breakpoint-up(lg) {
                margin-right: 40px;
            }
            @include media-breakpoint-up(xxl) {
                margin-right: 50px;
            }
        }
        .os-scrollbar-track {
            background: #F4F4F4;
            border-radius: 4px;
        }
        .os-scrollbar-handle {
            background: #C8C3C3;
            border-radius: 4px;
        }
    }
    .close-btn {
        background-color: transparent;
        border: none;
    }
    .Nk-h3 {
        @include media-breakpoint-down(md) {
            font-size: 18px;
        }
    }
    .fnt-20.clr-84 {
        @include media-breakpoint-down(md) {
            font-size: 12px;
        }
    }
}

.js-filter-content {
    background-color: #FFFFFF;
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 16px;
        border-bottom: 1px solid #DDDDDD;
        margin-bottom: 24px;
        @include media-breakpoint-up(md) {
            display: none;
        }
    }
    &.modal {
        display: block;
        @include media-breakpoint-up(md) {
            position: sticky;
            top: 0;
            z-index: 0;
            // width: auto;
        }
        @include media-breakpoint-down(md) {
            top: 0;
            transform: translateX(-100%);
            transition: .5s ease-in-out transform;
        }
    }
    // &.fade:not(.show) {
    //     @include media-breakpoint-up(md) {
    //         // opacity: 1;
    //     }
    // }
    &.show {
        @include media-breakpoint-down(md) {
            padding: 32px 15px;
            transform: translateX(0%);
            transition: .5s ease-in-out transform;
        }
    }
    .js-filter-close {
        img {
            width: 32px;
            height: 32px;
        }
    }
}

.mobile-filter-btns {
    display: none;
    @include media-breakpoint-down(md) {
        padding: 20px 45px;
        background: #FFFFFF;
        box-shadow: 8px 0px 30px rgba(0, 0, 0, 0.06);
        display: block;
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 99;
    }
}

.rangeslider--horizontal {
    height: 2px !important;
}

.rangeslider__handle {
    width: 14px !important;
    height: 14px !important;
    background-color: #0D131F !important;
    background-image: none !important;
    box-shadow: none !important;
    top: -7px !important;
    border: none !important;
    &::after {
        display: none
    }
}

.experiance__label-wrp {
    margin-top: 4px;
    display: flex;
    justify-content: space-between;
}

.rangeslider__handle:after {
    display: none !important;
}

.rangeslider__fill {
    background-color: #0D131F !important;
    height: 2px;
}

.custom-label {
    display: flex;
    align-items: center;
    justify-content: center;
    top: -50px;
    position: absolute;
    z-index: 9;
    width: 85px;
    height: 40px;
    background-color: white;
    text-align: center;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height, or 150% */
    letter-spacing: -0.04em;
    color: #000000;
    border: 1px solid #F1F1F1;
    box-sizing: border-box;
    box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.06);
    right: 50%;
    transform: translateX(50%);
    span {
        font-weight: 600;
        font-size: 10px;
        line-height: 16px;
        /* identical to box height, or 160% */
        letter-spacing: -0.04em;
        color: #000000;
        margin-left: 3px;
    }
    // background-image: url("../img/icons/label.svg");
    // background-repeat: no-repeat;
    // background-size: cover;
}

.custom-label::after {
    position: absolute;
    content: '';
    width: 15px;
    height: 15px;
    bottom: -4px;
    z-index: -4;
    background: white;
    transform: rotate( 45deg);
}