.service {
    padding-bottom: 100px;
    @include media-breakpoint-down(md) {
        padding-bottom: 40px;
    }
    &__lookFor {
        position: sticky;
        top: 0px;
        z-index: 99;
        background: #FFFFFF;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.05);
        padding: 25px 0;
        transition: top ease .5s;
        &.active {
            top: 80px;
            transition: top ease .5s;
        }
        @include media-breakpoint-down(md) {
            display: none;
        }
        &__list {
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 0;
        }
        &__item {
            &:not(:last-child) {
                margin-right: 80px;
                @include media-breakpoint-down(md) {
                    margin-right: 24px;
                }
            }
            a {
                text-decoration: none;
            }
        }
    }
    .service-wrp {
        position: relative;
        margin-top: 100px;
        @include media-breakpoint-down(md) {
            margin-top: 40px;
        }
    }
    &__content {
        margin-bottom: 24px;
        @include media-breakpoint-up(xl) {
            max-width: 440px;
            position: absolute;
            right: 0;
            top: 0;
        }
    }
    .service__item {
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: 260px 260px 260px 260px 260px;
        @include media-breakpoint-up(md) {
            grid-template-columns: 231px 231px 231px;
            grid-template-rows: 205px 205px 205px;
        }
        @include media-breakpoint-up(xl) {
            grid-template-columns: 281px 281px 281px;
            grid-template-rows: 205px 205px 205px;
        }
        @include media-breakpoint-up(xxl) {
            grid-template-columns: 351px 351px 351px;
            grid-template-rows: 235px 235px 235px;
        }
        &__wrp {
            &:nth-child(3) {
                @include media-breakpoint-up(md) {
                    grid-column-start: 1;
                }
            }
            &:nth-child(5) {
                @include media-breakpoint-up(md) {
                    grid-column-start: 2;
                }
            }
        }
    }
    .service__item__wrp {
        .flip-box {
            @include media-breakpoint-down(md) {
                border: 1px solid #C4C4C4;
            }
        }
        &:not(:last-child) {
            .flip-box {
                @include media-breakpoint-down(md) {
                    border-bottom: none;
                }
            }
        }
    }
    .flip-box {
        background-color: transparent;
        // width: 351px;
        // height: 235px;
        width: 100%;
        height: 100%;
        perspective: 1000px;
    }
    .flip-box-inner {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;
        transition: cubic-bezier(0.55, 0.055, 0.675, 0.19);
        // transition: transform 0.8s;
        // transform-style: preserve-3d;
    }
    .flip-box:hover .flip-box-inner {
        // transform: rotateY(180deg);
    }
    .flip-box-front,
    .flip-box-back {
        text-decoration: none;
        // position: absolute;
        // width: 100%;
        // height: 100%;
        // -webkit-backface-visibility: hidden;
        // backface-visibility: hidden;
        .fnt-20 {
            @include media-breakpoint-between(md, xxlg) {
                font-size: 18px;
                line-height: 26px;
            }
        }
    }
    .flip-box-front {
        // background-color: #fff;
        display: block;
        padding: 16px;
        position: relative;
        height: 100%;
        @include media-breakpoint-up(md) {
            background: rgba($color: #8BAAFF, $alpha: .1);
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            transition: cubic-bezier(0.55, 0.055, 0.675, 0.19) .5s;
            .card-content {
                display: block;
            }
            &__wrp {
                overflow: hidden;
                height: 100%;
                @include media-breakpoint-up(md) {
                    display: flex;
                    justify-content: space-between;
                    flex-direction: column;
                }
            }
            &:hover {
                .flip-box-front__img {
                    display: none;
                }
                background-color: #0D131F;
                transition: cubic-bezier(0.55, 0.055, 0.675, 0.19) .5s;
                p,
                h6 {
                    color: $white !important;
                }
                .card-content {
                    display: block !important;
                }
                &::after {
                    opacity: 1;
                }
            }
        }
        @include media-breakpoint-up(xl) {
            padding: 40px;
        }
        &::after {
            opacity: 0;
            content: "";
            position: absolute;
            bottom: 24px;
            right: 24px;
            color: #fff;
            background-image: url("../img/icons/white/right-arrow.svg");
            width: 7px;
            height: 11.84px;
            background-repeat: no-repeat;
            background-size: cover;
        }
        p {
            // margin-top: 24px;
            margin-top: 20px;
            margin-bottom: 8px;
            @include media-breakpoint-up(md) {
                display: none;
            }
        }
        &__img {
            margin-bottom: 8px;
            img {
                width: 30px;
                height: 30px;
                object-fit: contain;
                @include media-breakpoint-up(md) {
                    width: 50px;
                    height: 50px;
                }
            }
        }
    }
    .flip-box-back {
        // cursor: pointer;
        // background: #0D131F;
        // border: 1px solid #5F5F5F;
        // transform: rotateY(180deg);
        // color: #fff;
        // padding: 16px;
        // text-decoration: none;
        @include media-breakpoint-up(xl) {
            padding: 35px 30px 30px 40px;
        }
        &::after {
            content: "";
            position: absolute;
            bottom: 24px;
            right: 24px;
            color: #fff;
            background-image: url("../img/icons/white/right-arrow.svg");
            width: 7px;
            height: 11.84px;
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
}

