.fnt-24 {
    font-size: rem(18px);
    line-height: rem(28px);
    @include media-breakpoint-up(lg) {
        font-size: rem(21px) !important;
        line-height: rem(31px) !important;
    }
    @include media-breakpoint-up(xxlg) {
        font-size: rem(24px) !important;
        line-height: rem(32px) !important;
    }
}

.fnt-22 {
    font-size: rem(18px);
    line-height: rem(28px);
    @include media-breakpoint-up(lg) {
        font-size: rem(20px);
        line-height: rem(30px);
    }
    @include media-breakpoint-up(xxlg) {
        font-size: rem(24px);
        line-height: rem(34px);
    }
}

.fnt-21 {
    font-size: rem(16px);
    line-height: rem(26px);
    @include media-breakpoint-up(lg) {
        font-size: rem(18px);
        line-height: rem(28px);
    }
    @include media-breakpoint-up(xxlg) {
        font-size: rem(21px);
        line-height: rem(30px);
    }
}

.fnt-18 {
    font-size: rem(16px);
    line-height: rem(24px);
    @include media-breakpoint-up(lg) {
        font-size: rem(18px);
        line-height: rem(28px);
    }
}

