.kn-details {
    background: #0D131F;
    @include media-breakpoint-up(md) {
        height: 100vh;
        position: relative;
        display: flex;
        align-items: flex-end;
    }
    &__container {
        position: relative;
        z-index: 3;
        padding-bottom: 100px;
        @include media-breakpoint-down(xxlg) {
            padding-bottom: 48px;
        }
        @include media-breakpoint-down(lg) {
            padding-top: 32px;
            padding-bottom: 24px;
        }
        .h1 {
            margin-top: 40px;
        }
    }
    &__img {
        height: 354px;
        position: relative;
        @include media-breakpoint-up(md) {
            height: 100%;
            width: 100%;
            position: absolute;
        }
        img {
            object-position: left;
            object-fit: cover;
            height: inherit;
            width: 100%;
        }
        &::after {
            position: absolute;
            content: '';
            background: radial-gradient(50% 50% at 50% 50%, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.6) 100%);
            background-size: 100%;
            z-index: 2;
            width: 100%;
            height: 100%;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
        }
    }
}

.sub-text {
    font-weight: 600;
    font-size: 20px;
    line-height: 32px;
    text-transform: uppercase;
    color: #FFFFFF;
    opacity: 0.8;
    @include media-breakpoint-down(md) {
        font-size: 12px;
        line-height: 16px;
    }
}

.kn-info {
    &__img {
        width: 100%;
        height: 552px;
        object-fit: cover;
        @include media-breakpoint-down(md) {
            height: 200px;
        }
    }
}

.mb-40 {
    margin-bottom: 40px;
    @include media-breakpoint-down(md) {
        margin-bottom: 16px;
    }
}

.tags-wrap {
    display: flex;
    align-items: center;
    margin-top: 40px;
    flex-wrap: wrap;
    @include media-breakpoint-down(md) {
        margin-top: 24px;
        margin-bottom: 8px;
    }
    li {
        margin-right: 36px;
    }
    .tags {
        padding: 2px 16px;
        border: 1px solid #C4C4C4;
        font-weight: 600;
        font-size: 12px;
        line-height: 28px;
        text-transform: uppercase;
        color: #0D131F;
    }
}

.border-top-g {
    border-top: 1px solid #C4C4C4;
}

.more-details {
    &__card {
        &__item {
            background: #F4F4F4;
            padding: 32px;
            display: flex;
            margin-bottom: 40px;
            @include media-breakpoint-down(md) {
                padding: 16px;
                margin-bottom: 8px;
            }
            .img-wrp {
                @include media-breakpoint-down(md) {
                    display: none;
                }
            }
            &__img {
                position: relative;
                width: 158px;
                height: 100%;
                // height: 135px;
                img {
                    position: absolute;
                    object-fit: cover;
                    bottom: -32px;
                }
            }
            &__body {
                @include media-breakpoint-up(xl) {
                    padding-left: 30px;
                }
                @include media-breakpoint-up(lg) {
                    padding-left: 4px;
                }
                h6 {
                    font-size: 24px;
                    line-height: 32px;
                    /* identical to box height, or 133% */
                    text-transform: capitalize;
                    color: #0D131F;
                    @extend .fnt-700;
                    @include media-breakpoint-down(md) {
                        font-size: 16px;
                        line-height: 24px;
                        margin-bottom: 4px;
                    }
                }
                span {
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 24px;
                    /* identical to box height, or 171% */
                    text-transform: uppercase;
                    color: #0D131F;
                    opacity: 0.8;
                    @include media-breakpoint-down(md) {
                        font-size: 12px;
                        line-height: 20px;
                    }
                }
                p {
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 24px;
                    /* or 171% */
                    color: #0D131F;
                    opacity: 0.8;
                    margin-top: 16px;
                    @include media-breakpoint-down(md) {
                        font-size: 12px;
                        line-height: 20px;
                        margin-top: 12px;
                    }
                }
            }
        }
        &__icons {
            .wrp {
                display: flex;
                img:first-child {
                    margin-right: 8px;
                }
            }
        }
    }
}

.interested {
    background: rgba($color: #8BAAFF, $alpha: .1);
    .btn {
        width: auto;
    }
}