.bg-Primary {
    padding: 12px;
    background: $primary;
    .h2 {
        color: $bg-pri !important;
        @include media-breakpoint-down(md) {
            position: absolute;
            color: $bg-pri !important;
            bottom: 70%;
            z-index: 1;
            left: 0;
            right: 0;
            text-align: center;
            transform: translate(0%, 50%);
            margin: 0 auto;
            display: block;
            width: 70%;
        }
    }
    .zw-section-lead__des {
        color: $txt-clr !important;
    }
    // a {
    //   color: $bg-thrice !important;
    //   text-decoration: none;
    // }
}

.zw-detail-banner {
    position: relative;
    @include media-breakpoint-down(md) {
        padding-right: 1rem;
        padding-left: 1rem;
    }
    &__item {
        padding: 24px;
        @include media-breakpoint-up(lg) {
            padding: 50px;
        }
        @include media-breakpoint-up(xl) {
            padding: 70px;
        }
        @include media-breakpoint-up(xxl) {
            padding: 90px 120px;
        }
    }
}

