.zw-bg-thrice {
    position: relative;
    @extend .zw-section-margin50;
    @include media-breakpoint-down(md) {
        overflow: hidden;
    }
    &__item {
        background: $bg-thrice;
        padding: 24px;
        @include media-breakpoint-up(lg) {
            padding: 50px;
        }
        @include media-breakpoint-up(xl) {
            padding: 70px;
        }
        @include media-breakpoint-up(xxl) {
            padding: 90px 120px;
        }
    }
}

.bg-size-blue-block {
    background: $bg-sec;
    &__item {
        padding: 24px;
        @include media-breakpoint-up(lg) {
            padding: 50px;
        }
        @include media-breakpoint-up(xl) {
            padding: 70px;
        }
        @include media-breakpoint-up(xxl) {
            padding: 90px 120px;
        }
    }
    .h2 {
        color: $bg-pri !important;
    }
    .zw-section-lead__des {
        color: $txt-clr !important;
    }
    a {
        // color: $bg-thrice !important;
        color: $bgThrice06 !important;
        text-decoration: none;
    }
}