.zw-slider-controlers-wrap {
    position: absolute;
    bottom: 1.8rem;
    left: 0;
    right: 0;
    z-index: 1;
}

.swiper-button-prev,
.swiper-button-next {
    position: relative !important;
    margin: 0.5rem;
    &:focus {
        outline: none;
    }
}

.swiper-button-prev {
    // opacity: 1 !important;
    left: 0 !important;
}

.swiper-button-next {
    right: 0 !important;
}

.zw-circle {
    border-radius: 50%;
    border: 3px solid $bg-pri;
    cursor: pointer;
    &--sec {
        border-color: #4c4a48;
    }
    &--lg {
        height: rem(70px);
        width: rem(70px);
    }
    &--sm {
        height: rem(50px);
        width: rem(50px);
        transition: $transition;
        &:hover {
            background: $bg-pri;
            color: $black;
            transition: $transition;
            path {
                stroke: $black;
            }
        }
    }
}

.js-zw-hero-list {
    @include media-breakpoint-up(md) {
        border: 1px solid #e6dfd4;
    }
    .zwPanel {
        cursor: pointer;
        &:after {
            content: "";
            position: absolute;
            height: 100%;
            width: 1px;
            background: #e6dfd4;
            top: 0;
            bottom: 0;
            right: 0;
        }
        &:last-child::after {
            display: none;
        }
        &:hover {
            &:after {
                background: $black;
            }
        }
    }
}

// right arrow
.zw-heading-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.zw-slider-controlers-right-wrap {
    position: relative !important;
    z-index: 1;
    display: flex;
    .swiper-button-prev,
    .swiper-button-next {
        top: 0;
    }
}

.zw-leadership-list {
    @include media-breakpoint-down(md) {
        background: $bg-thrice;
        .-description {
            background: transparent;
        }
    }
}

.js-zw-leadership-list {
    .swiper-slide {
        padding: 0 0.5rem;
        position: relative;
        &:hover {
            .inf04 {
                opacity: 1;
                transition: 0.5s;
                display: block;
            }
        }
    }
    .swiper-wrapper {
        margin: 0 -0.4rem !important;
        .swiper-slide:last-child {
            padding-right: 0;
        }
    }
}

.-description {
    &__block {
        justify-content: space-between;
        align-items: center;
        // @extend .borderSecondBottom;
        @include media-breakpoint-up(xl) {
            display: flex;
        }
    }
    &__name {
        @extend .fnt-24;
        @extend .fnt-700;
        @extend .my-0;
        @extend .fnt-gow-bold;
        @extend .text-capitalize;
        @extend .pe-lg-5;
        @include media-breakpoint-up(lg) {
            padding-right: 1rem !important;
        }
    }
}

.-description {
    width: 100%;
    background: $bg-pri;
    padding: 20px 0;
    bottom: 0;
    position: relative;
    transition: 0.5s;
    @include media-breakpoint-up(xl) {
        bottom: -3px;
    }
    @include media-breakpoint-up(xl) {
        bottom: 57px;
    }
}

.inf04 {
    transition: 0.5s;
    @include media-breakpoint-up(xl) {
        opacity: 0;
        // display: none;
    }
    @extend .zw-section-lead__des;
    @extend .zm-clr-gr4c;
    @extend .my-3;
    @extend .text-truncate;
}

.box .zw-leader {
    transition: 0.5s;
    @include media-breakpoint-up(xl) {
        flex: 1;
        overflow: hidden;
        height: 566px !important;
    }
}

.box .zw-leader>img {
    width: 100%;
    // height: calc(100% - 10vh) !important;
    object-fit: cover;
    transition: 0.5s;
    filter: grayscale(100%);
    @include media-breakpoint-up(xl) {
        bottom: 0;
    }
}

.box:hover .zw-leader {
    .-description {
        transition: 0.5s;
        @include media-breakpoint-up(xl) {
            bottom: 100px;
        }
    }
    >img {
        transition: 0.5s;
        width: 100%;
        filter: grayscale(0%);
        @include media-breakpoint-up(xl) {
            height: 100% !important;
        }
    }
}