.nk-footer {
    background: #0D131F;
    .form-input {
        background-color: transparent;
        border: 1px solid rgba($color: #C1C1C1, $alpha: .2);
        box-sizing: border-box;
        border-radius: 54px;
        padding: 16px 26px;
        outline: none;
        color: $white;
        width: 100%;
        &::placeholder {
            font-weight: 600;
            font-size: 14px;
            line-height: 33px;
            color: rgba($color: #C1C1C1, $alpha: .6);
        }
    }
    & .fp-tableCell {
        height: auto !important;
    }
    a:hover {
        color: $white;
    }
    &__links {
        margin-top: 100px;
        @include media-breakpoint-down(md) {
            margin-top: 32px;
        }
        li {
            margin-bottom: 13px;
            @include media-breakpoint-down(md) {
                margin-bottom: 2px;
            }
        }
        p,
        a {
            font-weight: 600;
            font-size: 14px;
            line-height: 26px;
            color: #FFFFFF;
            text-decoration: none;
            @include media-breakpoint-up(md) {
                font-size: 16px;
            }
        }
        a {
            color: rgba($color: #fff, $alpha: .6);
        }
    }
    .subscribe {
        position: relative;
        margin-top: 32px;
    }
    .subscribe-btn {
        font-weight: 700;
        position: absolute;
        font-size: 16px;
        line-height: 26px;
        /* identical to box height, or 162% */
        text-align: center;
        color: #FFFFFF;
        right: 36px;
        top: 50%;
        transform: translateY(-50%);
        outline: none;
        border: none;
        background-color: transparent;
    }
    .social-media-wrp {
        padding-top: 24px;
        &.b-mbl {
            border-top: 1px solid rgba($color: #C1C1C1, $alpha: .1);
        }
        @include media-breakpoint-up(lg) {
            border-top: none;
            padding-top: 32px;
        }
    }
    .social-media {
        display: flex;
        justify-content: end;
        align-items: end;
        &__link {
            margin-left: 24px;
            img {
                transition: transform .5s ease;
            }
            &:hover {
                img {
                    transform: scale(1.2);
                    transition: transform .5s ease;
                }
            }
        }
    }
}

.ourTeam__card__footer {
    .wrp {
        a {
            img {
                transition: transform .5s ease;
            }
            &:hover {
                img {
                    transform: scale(1.2);
                    transition: transform .5s ease;
                }
            }
        }
    }
}