.advisory {
    &__card {
        margin-bottom: 40px;
        @include media-breakpoint-down(md) {
            margin-bottom: 24px;
        }
        &__img {
            width: 100%;
            height: 544px;
            object-fit: cover;
            position: relative;
            overflow: hidden;
            img {
                transform: scale(1);
                transition: .5s ease all;
                &:hover {
                    transform: scale(1.2);
                    transition: .5s ease all;
                }
            }
            @include media-breakpoint-down(xl) {
                height: 380px;
            }
            &::after {
                position: absolute;
                content: '';
                background: linear-gradient(358.65deg, #000000 8.32%, rgba(13, 19, 31, 0) 89.81%);
                opacity: 0.8;
                bottom: 0;
                width: 100%;
                height: 308px;
                left: 0;
            }
            &>img {
                width: 100%;
                height: 100%;
            }
            &:hover {
                .gsap-cardFooter {
                    display: block;
                    transform: translateY(0);
                    transition: all ease-in-out .5s;
                }
            }
        }
        &__body {
            padding: 40px;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            z-index: 2;
            transition: .5s ease all;
            @include media-breakpoint-down(md) {
                padding: 24px;
            }
            .name {
                font-size: 32px;
                line-height: 32px;
                /* identical to box height, or 100% */
                text-transform: capitalize;
                color: #FFFFFF;
                font-weight: 700;
                @include media-breakpoint-down(md) {
                    font-size: 24px;
                    line-height: 32px;
                    margin-bottom: 4px;
                }
            }
            .position {
                font-weight: 600;
                font-size: 20px;
                line-height: 32px;
                text-transform: uppercase;
                color: #FFFFFF;
                opacity: 0.8;
                @include media-breakpoint-down(md) {
                    font-size: 12px;
                    line-height: 16px;
                }
            }
            .gsap-cardFooter {
                display: none;
                transform: translateY(-200px);
                transition: all ease-in-out .5s;
            }
            &__footer {
                margin-top: 20px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                @include media-breakpoint-down(xl) {
                    flex-direction: column;
                    justify-content: flex-start;
                    align-items: flex-start;
                }
                .btn {
                    @include media-breakpoint-down(md) {
                        width: 200px;
                    }
                }
                .wrp {
                    img:first-child {
                        margin-right: 8px;
                    }
                    @include media-breakpoint-down(xl) {
                        margin-top: 8px;
                    }
                }
            }
        }
    }
    &__imgwrp {
        margin-top: 60px;
        @include media-breakpoint-down(md) {
            margin-top: 32px;
        }
        &.gx-lg-5 {
            --bs-gutter-x: 40px
        }
    }
}

.ourTeam {
    background: #0D131F;
    .gx-lg-5 {
        @include media-breakpoint-up(md) {
            --bs-gutter-x: 40px
        }
    }
    &__card {
        margin-bottom: 100px;
        @include media-breakpoint-down(lg) {
            margin-bottom: 42px;
        }
        @include media-breakpoint-down(md) {
            margin-bottom: 32px;
        }
        &__img {
            background: #F4F4F4;
            text-align: center;
            // width: 320PX;
            height: 380px;
            display: flex;
            justify-content: center;
            overflow: hidden;
            @include media-breakpoint-down(xxl) {
                height: 295px;
            }
            @include media-breakpoint-down(xl) {
                height: 270px;
            }
            @include media-breakpoint-down(md) {
                height: 170px;
            }
            &:hover {
                img {
                    transform: scale(1.1);
                    transition: .5s ease all;
                }
            }
            img {
                width: 100%;
                margin-top: auto;
                object-fit: cover;
                transition: .5s ease all;
                @include media-breakpoint-down(md) {
                    height: 90%;
                }
                // width: 265px;
                // width: 320PX;
                // height: 380px;
            }
        }
        .position {
            font-weight: 600;
            font-size: 14px;
            line-height: 24px;
            text-transform: uppercase;
            color: #FFFFFF;
            opacity: 0.8;
        }
        .wrp {
            margin-top: 12px;
            img:first-child {
                margin-right: 8px;
            }
        }
        &__footer {
            display: flex;
            align-items: center;
            justify-content: space-between;
            a {
                color: rgba($color: #fff, $alpha: .8);
                text-decoration: none;
            }
        }
    }
}

