.core-value {
    padding-top: 140px;
    background: rgba($color: #8BAAFF, $alpha: .1);
    @include media-breakpoint-down(lg) {
        padding-top: 24px;
    }
    &__list {
        padding-top: 120px;
        display: flex;
        flex-wrap: wrap;
        @include media-breakpoint-down(lg) {
            padding-top: 32px;
        }
        &__item {
            border-right: 1px solid rgba($color: #000000, $alpha: .3);
            margin-bottom: 120px;
            width: calc(100%/3);
            @include media-breakpoint-down(lg) {
                width: calc(100%/2);
                margin-bottom: 44px;
            }
            @include media-breakpoint-down(md) {
                width: 100%;
                border-right: 0;
                margin-bottom: 44px;
            }
            &:nth-child(3n),
            &:last-child {
                @include media-breakpoint-up(xl) {
                    border-right: 0;
                }
            }
            &:first-child {
                .core-value__list__item__wrp {
                    margin-left: 0;
                }
            }
            &:nth-child(4n) {
                @include media-breakpoint-up(xl) {
                    .core-value__list__item__wrp {
                        margin-left: 0;
                    }
                }
            }
            &__wrp {
                max-width: 320px;
                margin: auto;
                @include media-breakpoint-down(xl) {
                    max-width: 280px;
                }
                @include media-breakpoint-down(md) {
                    margin-left: 0;
                }
            }
            .Nk-h3 {
                margin-top: 35px;
                margin-bottom: 24px;
                color: #051D60;
                font-weight: 600;
                @include media-breakpoint-down(xxlg) {
                    margin-top: 12px;
                    margin-bottom: 12px;
                }
            }
            &__logo {
                width: 40px;
                height: 40px;
                object-fit: cover;
            }
        }
    }
}

.aboutModel {
    .modal-dialog {
        max-width: 95%;
        @include media-breakpoint-up(xxl) {
            max-width: 1440px;
            width: 100%;
        }
    }
    h3 {
        font-size: 48px;
        line-height: 32px;
        /* identical to box height, or 67% */
        text-transform: capitalize;
        font-weight: 700;
        color: #FFFFFF;
        @include media-breakpoint-down(lg) {
            font-size: 18px;
            line-height: 24px;
            /* identical to box height, or 133% */
            letter-spacing: -0.04em;
            color: #FFFFFF;
        }
    }
    .modal-body {
        padding: 0;
    }
    .modal-content {
        padding: 24px;
        background: #0D131F;
        @include media-breakpoint-up(lg) {
            padding: 32px 24px 32px 32px;
        }
        @include media-breakpoint-up(xxl) {
            padding: 64px 42px 64px 64px;
        }
    }
    &__wrp {
        display: flex;
        @include media-breakpoint-down(md) {
            flex-direction: column;
        }
    }
    &__body {
        width: 100%;
        // margin-right: 24px;
        @include media-breakpoint-down(md) {
            margin-top: 15px;
        }
        @include media-breakpoint-up(md) {
            padding-left: 32px;
        }
        @include media-breakpoint-up(xxl) {
            padding-left: 64px;
        }
        .wrp {
            padding-right: 16px;
        }
        .logoWro {
            &__wrp {
                padding-left: 16px;
                border-left: 1px solid #FFFFFF;
            }
        }
        &__head {
            margin-bottom: 32px;
            display: flex;
            @include media-breakpoint-down(xl) {
                margin-bottom: 24px;
            }
            img {
                width: 32px;
                height: 32px;
                @include media-breakpoint-down(md) {
                    width: 16px;
                    height: 16px;
                }
            }
            .fnt-24 {
                @include media-breakpoint-down(md) {
                    font-size: 12px;
                    line-height: 16px;
                }
            }
            .fnt-20 {
                @include media-breakpoint-down(md) {
                    font-size: 14px;
                    line-height: 24px;
                    color: #FFFFFF;
                    opacity: 0.8;
                }
            }
        }
        &__info {
            &__foot {
                display: flex;
                padding-top: 24px;
                border-top: 1px solid rgba($color: #fff, $alpha: .4);
                @include media-breakpoint-down(md) {
                    flex-direction: column;
                }
                &__img {
                    @include media-breakpoint-down(md) {
                        width: 40px;
                    }
                    img {
                        width: 100%;
                    }
                }
            }
        }
    }
    &__img {
        // position: relative;
        img {
            width: 227.6px;
            height: 281px;
            object-fit: cover;
            @include media-breakpoint-up(lg) {
                width: 350px;
                height: 440px;
            }
            @include media-breakpoint-up(xxl) {
                width: 520px;
                height: 642px;
            }
        }
        .position-relative {
            width: 227.6px;
            height: 281px;
            object-fit: cover;
            overflow: hidden;
            @include media-breakpoint-up(lg) {
                width: 350px;
                height: 440px;
            }
            @include media-breakpoint-up(xxl) {
                width: 520px;
                height: 642px;
            }
            img {
                transform: scale(1);
                transition: .5s ease all;
                &:hover {
                    transform: scale(1.2);
                    transition: .5s ease all;
                }
            }
            &::after {
                position: absolute;
                content: '';
                bottom: 0;
                left: 0;
                right: 0;
                width: 100%;
                height: 363.49px;
                background: linear-gradient(358.65deg, #000000 8.32%, rgba(13, 19, 31, 0) 89.81%);
                opacity: 0.8;
            }
        }
    }
    .aboutModel__body__info {
        height: 324px;
        margin-bottom: 50px;
        @include media-breakpoint-down(lg) {
            margin-bottom: 32px;
        }
        p {
            margin-bottom: 20px;
        }
    }
    .btn-close {
        background-image: none;
        position: absolute;
        top: 32px;
        right: 42px;
        opacity: 1;
        z-index: 99;
        @include media-breakpoint-down(lg) {
            top: 16px;
            right: 26px;
        }
    }
}

