/*
Color Codes
*/

$transparent: transparent;
$primary: #202020;
$second: #4c4a48;
$thrice: #3d69d4;
$blue: #051D60;
// secondary colors
$tin-blue: #0047ff;
$tin-red: #ff0008;
$dang: #e91e63;
$tick-blue: #0084ff;
$tick-gray: #c4c4c4;
$tin-gray: #bebebe;
// BASICS COLORS
$bg-pri: #faf3e7;
$bg-sec: #192954;
$bg-thrice: #e4ded3;
$black: #000000;
$bd:#0D131F;
$white: #ffffff;
$txt-clr: rgba(#e4ded3, 0.9);
$txt-clr06: rgba($txt-clr, 0.6);
$second09: rgba($second, 0.9);
$second06: rgba($second, 0.6);
$second02: rgba($second, 0.2);
$black01: rgba($black, 0.1);
$bgThrice06: rgba($bg-thrice, 0.6);
// Background

/*
 Background gradients
*/

$bg-gradient-pri: linear-gradient( 180deg, rgba(236, 240, 253, 0) 0%, rgba(236, 240, 253, 0.53) 14%, #ecf0fd 46%, rgba(236, 240, 253, 0.43) 84%, rgba(236, 240, 253, 0) 100%);
$bg-gradient-sec: linear-gradient( 180deg, rgba(236, 240, 253, 0) 0%, rgba(236, 240, 253, 0.53) 37%, rgba(236, 240, 253, 0.43) 78%, rgba(236, 240, 253, 0) 100%);

/*
 Fonts Start
*/

$fnt-m:'Mulish',
sans-serif;

/*
 Fonts End
*/

// font-weights
$fnt-400: 400;
$fnt-500: 500;
$fnt-700: 700;
$fnt-600: 600;
$fnt-bold: bold;
// Transtion
$transition: all 0.6s ease;
// Border collor
// Boxshadow
$boxshadow: 0px 16px 16px rgba(17, 29, 59, 0.15);